<template>
  <div id="app" class="premium-bg">
    <!-- Overlay for darkening the background -->
    <div class="overlay"></div>

    <div class="content-wrapper">
      <header class="main-header">
        <div class="logo">LUXE<span>ESTATE</span></div>
        <div class="divider"></div>
        <p class="tagline">ПЕРСОНАЛЬНИЙ ПІДБІР НЕРУХОМОСТІ</p>
      </header>

      <main class="quiz-container"> 
        <transition name="fade" mode="out-in">
          <!-- Step 1: Deal Type -->
          <div v-if="step === 1" :key="1" class="step-card">
            <h1 class="premium-title">Який стиль життя <br/>Ви обираєте сьогодні?</h1>
            <div class="deal-options">
              <div class="deal-card" @click="selectDeal('rent')">
                <div class="card-bg rent-bg"></div>
                <div class="card-content">
                  <span>АРЕНДА</span>
                  <p>Довгостроковий комфорт</p>
                </div>
              </div>
              <div class="deal-card" @click="selectDeal('buy')">
                <div class="card-bg buy-bg"></div>
                <div class="card-content">
                  <span>КУПІВЛЯ</span>
                  <p>Інвестиція у майбутнє</p>
                </div>
              </div>
            </div>
          </div>

          <!-- Step 2: Rooms -->
          <div v-else-if="step === 2" :key="2" class="step-card">
            <h2 class="premium-title">Кількість кімнат</h2>
            <div class="rooms-grid">
              <button v-for="r in ['1', '2', '3', '4+']" 
                      :key="r" 
                      @click="toggleRoom(r)"
                      :class="{ active: formData.rooms.includes(r) }"
                      class="room-btn">
                {{ r }}
              </button>
            </div>
            <div class="actions">
              <button @click="nextStep" :disabled="formData.rooms.length === 0" class="gold-btn">ПРОДОВЖИТИ</button>
            </div>
          </div>

          <!-- Step 3: Cities -->
          <div v-else-if="step === 3" :key="3" class="step-card">
            <h2 class="premium-title">Локація</h2>
            <div class="city-list">
              <div v-for="(city, id) in cities" :key="id" @click="selectCity(id)" class="city-row">
                <span class="city-name">{{ city.name }}</span>
                <span class="city-arrow">→</span>
              </div>
            </div>
            <button @click="prevStep" class="back-btn">Повернутись</button>
          </div>

          <!-- Step 4: Districts -->
          <div v-else-if="step === 4" :key="4" class="step-card">
            <h2 class="premium-title">{{ cities[formData.city].name }}: Райони</h2>
            <div class="districts-cloud">
              <div v-for="d in cities[formData.city].districts" 
                   :key="d" 
                   @click="toggleDistrict(d)"
                   :class="{ selected: formData.districts.includes(d) }"
                   class="district-tag">
                {{ d }}
              </div>
            </div>
            <div class="actions">
              <button @click="nextStep" :disabled="formData.districts.length === 0" class="gold-btn">ДАЛІ</button>
            </div>
          </div>

          <!-- Step 5: Budget -->
          <div v-else-if="step === 5" :key="5" class="step-card">
            <h2 class="premium-title">Бюджетний діапазон</h2>
            <div class="budget-grid">
              <button v-for="b in budgetOptions" :key="b" @click="selectBudget(b)" class="budget-btn">
                {{ b }}
              </button>
            </div>
            <button @click="prevStep" class="back-btn">Назад</button>
          </div>

          <!-- Step 6: Final -->
          <div v-else-if="step === 6" :key="6" class="step-card">
            <h2 class="premium-title">Конфіденційність</h2>
            <p class="step-desc">Менеджер підготує закриту презентацію об'єктів для Вас</p>
            <div class="contact-form">
              <div class="input-group">
                <input type="text" v-model="formData.name" placeholder="Ваше ім'я" />
              </div>
              <div class="input-group">
                <input type="tel" v-model="formData.phone" placeholder="+380" />
              </div>
              <button @click="submitForm" class="submit-btn" :disabled="loading">
                {{ loading ? 'ВІДПРАВКА...' : 'ОТРИМАТИ ПРЕЗЕНТАЦІЮ' }}
              </button>
            </div>
          </div>

          <!-- Success -->
          <div v-else-if="step === 7" :key="7" class="step-card success">
            <div class="gold-check">✓</div>
            <h2 class="premium-title">Запит прийнято</h2>
            <p>Очікуйте дзвінок нашого консультанта протягом 15 хвилин.</p>
            <button @click="resetQuiz" class="back-btn">Головна</button>
          </div>
        </transition>
      </main>

      <footer class="main-footer">
        <p>© 2024 LUXE ESTATE AGENCY | ПРИВАТНІСТЬ ТА ЕКСКЛЮЗИВНІСТЬ</p>
      </footer>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      step: 1,
      loading: false,
      formData: { deal: '', rooms: [], city: '', districts: [], budget: '', name: '', phone: '' },
      budgetOptions: ['До 20 000', '20 000 - 40 000', '40 000 - 80 000', '80 000+'],
      cities: {
        kiev: { name: 'Київ', districts: ['Печерськ', 'Центр', 'Поділ', 'Оболонь', 'Голосієво'] },
        odessa: { name: 'Одеса', districts: ['Аркадія', 'Центр', 'Фонтан', 'Таїрова'] },
        lviv: { name: 'Львів', districts: ['Центр', 'Сихів', 'Франківський'] }
      }
    }
  },
  methods: {
    nextStep() { this.step++ },
    prevStep() { this.step-- },
    selectDeal(type) { this.formData.deal = type; this.nextStep(); },
    toggleRoom(r) {
      const idx = this.formData.rooms.indexOf(r);
      if (idx > -1) this.formData.rooms.splice(idx, 1);
      else this.formData.rooms.push(r);
    },
    selectCity(id) { this.formData.city = id; this.nextStep(); },
    toggleDistrict(d) {
      const idx = this.formData.districts.indexOf(d);
      if (idx > -1) this.formData.districts.splice(idx, 1);
      else this.formData.districts.push(d);
    },
    selectBudget(val) { this.formData.budget = val; this.nextStep(); },
    async submitForm() {
  if (!this.formData.phone) return;
  this.loading = true;

  // Підготовка даних у форматі URLSearchParams (як того очікує e.parameter у GAS)
  const params = new URLSearchParams();
  params.append('deal', this.formData.deal);
  params.append('name', this.formData.name || 'Клієнт');
  params.append('phone', this.formData.phone);
  params.append('city', this.cities[this.formData.city]?.name || '');
  params.append('rooms', this.formData.rooms.join(', '));
  params.append('districts', this.formData.districts.join(', '));
  params.append('budget', this.formData.budget);

  const GOOGLE_SCRIPT_URL = 'https://script.google.com/macros/s/AKfycby20HowX1TpXxCCIAgfCSbB1xp1an1ORVeDqAi_OzA7PicwTltbQ5Knf2NDDLt01UPK/exec';

  try {
    // Використовуємо mode: 'no-cors', бо Google Script робить редирект, 
    // який браузери часто блокують через політику безпеки.
    // У режимі no-cors ми не побачимо відповідь "success", 
    // але дані в таблицю потраплять.
    await fetch(GOOGLE_SCRIPT_URL, {
      method: 'POST',
      mode: 'no-cors', 
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
      },
      body: params.toString()
    });

    // Оскільки ми в no-cors, ми не можемо перевірити response.ok.
    // Припускаємо, що все ок, і переходимо до фінального кроку.
    this.step = 7;

  } catch (e) {
    console.error("Помилка:", e);
    alert("Сталася помилка при відправці.");
  } finally {
    this.loading = false;
  }
},
    resetQuiz() {
      this.step = 1;
      this.formData = { deal: '', rooms: [], city: '', districts: [], budget: '', name: '', phone: '' };
    }
  }
}
</script>

<style>
