/* Deal Cards */
.deal-options { display: grid; grid-template-columns: 1fr 1fr; gap: 20px; }
.deal-card {
  position: relative; height: 300px; border-radius: 4px; overflow: hidden; cursor: pointer;
  border: 1px solid rgba(255,255,255,0.1); transition: transform 0.4s ease;
}
